#header {
  background-color: pink;
  display: flex;
  justify-content: center;
}

#header .center {
  width: 1200px;
  background-color: green;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#header .center .logo a {
  color: red;
}

#header .center .gnb {
  display: flex;
}

#header .center .gnb > li {
  margin: 0 15px;
  line-height: 100px;
}

#header .center .gnb > li > a {
  color: blue;
}